.parallax-quote-banner {

    @include highlight-override($white, $brand-cta);

    blockquote {
        border: none;
        padding: 0;
        margin: 0 auto;
        text-align: center;
        max-width: 400px;
        font-size: map-deep-get($heading-font-sizes, "h5", "small") * $font-size-large-multiplier;

        p {
            &:before {
                content: open-quote;
            }

            &:after {
                content: close-quote;
            }
        }

        @media #{$medium-up} {
            max-width: 700px;
            font-size: map-deep-get($heading-font-sizes, "h5", "large") * $font-size-large-multiplier;
        }
    }

    footer {
        &:before, cite {
            color: $white;
        }
    }

}